import React from 'react';
import { withRouter } from 'react-router';
import { FormattedMessage, FormattedHTMLMessage } from 'react-intl';
import '../../styles/main.css'
import Section from '../hoc/Section';
import { Row, Col1_3, Col2_3 } from '../hoc/Grid';
import Page from '../hoc/Page';
import { connect } from 'react-redux';

const language = navigator.language.split(/[-_]/)[0];

const TherapistDetails = (props) => {

    function handleSelection(id, props) {
        // this.props.selectTherapist(id);
        props.history.push("/schedule");
    }

    console.log(props.location.state.state);

    const info = props.location.state.state;

    const questions = {
        'therapist_details.question_1': info[language].answers[0],
        'therapist_details.question_2': info[language].answers[1],
        'therapist_details.question_3': info[language].answers[2]
    }

    console.log(info.picture_url)
    return (

        <Page>
            <Section>
                <Row>
                    <Col1_3>
                        <img alt="avatar" src={info.picture_url} className="float-right avatar round avatar-xxl" />
                    </Col1_3>

                    <Col2_3 classes="height_avatar_xl">
                        <div className="large-title">
                            <h2 className="no-left-margin"><mark>{info.name}</mark></h2>
                        </div>
                        <label className="qoute default-bottom-margin">{info[language].qoute}</label>
                        <div>
                            <label className="info-title"><FormattedMessage id="therapist_details.age" /></label>
                            <label>{info[language].age}</label>
                        </div>
                        <div >
                            <label className="info-title"><FormattedMessage id="therapist_details.background" /></label>
                            <label>{info[language].studies}</label>
                        </div>
                        <div>
                            <label className="info-title"><FormattedMessage id="therapist_details.experience" /></label>
                            <label>{info[language].experience}</label>
                        </div>
                        <div>
                            <label className="info-title"><FormattedMessage id="therapist_details.focus" /></label>
                            <label>{info[language].speciality}</label>
                        </div>
                    </Col2_3>
                </Row>
                <Row>
                    <Col1_3></Col1_3>
                    <Col2_3>
                        <button className="btn-primary" onClick={() => { handleSelection(info.name, props) }}>
                            <FormattedMessage
                                id="therapist_details.schedule_appointment"
                                values={{ name: info.name }} >{text => text}</FormattedMessage>
                        </button>
                    </Col2_3>
                </Row>
                {
                    Object.entries(questions).map(([key, value]) =>
                        <Row classes="mobile-margin">
                            <p className="lead mobile-margin">
                                <FormattedHTMLMessage id={key} />
                            </p>
                            <p className="mobile-margin">
                                <FormattedHTMLMessage id={value} />
                            </p>
                        </Row>
                    )
                }

            </Section >
        </Page>
    );

}

const mapStateToProps = state => {
    return {
        // name: state.value //-> to tuse this.props.name
    };
}

const mapDispatchToProps = dispatch => {
    return {
        selectTherapist(name) {
            console.log(name);
            dispatch({
                type: 'SELECT_THERAPIST',
                name: name
            })
            // -> to user this.props.name
            // to user in reducer action.value or action.payload
        }
    }
}

// export default connect(mapStateToProps, mapDispatchToProps)(withRouter(TherapistDetails));

export default withRouter(TherapistDetails);