const initialState = {
    username: "",
    sessions: [],
    selectedTherapist: null,
    selectedProblems: [],
    selectedSymptoms: [],
    selectedTimes: []
}

const reducer = (state = initialState, action) => {
    switch (action) {
        case 'ADD_TIME': {
            return {
                ...state, // distribute the old state - immutably
                selectedTimes: state.selectedTimes.concat(action.value)
            }
        }
    }
};

export default reducer;