import React from 'react';
import { FormattedMessage } from 'react-intl';
import Slider from '@material-ui/core/Slider';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import { language, messages } from '../../index';
import { Card, Icon } from '../hoc/Grid';
import {
  icon_stressed,
  icon_box_tissue,
  icon_dringende_nachricht,
  icon_epilepsy_smartwatch,
  icon_fernglas,
  icon_finanzmedaille,
  icon_fragen,
  icon_frau_mit,
  icon_freunde,
  icon_gefährliche_strömung,
  icon_hochzeitstag,
  icon_info,
  icon_konferenztelefonat,
  icon_konflikt,
  icon_kopfschmerzen,
  icon_meditationsguru,
  icon_person_im,
  icon_schlaflosigkeit_durchmachen,
  icon_smartphone_tablet,
  icon_unternehmen,
  icon_vater,
  icon_yoga,
  icon_zum_schweigen
} from '../hoc/Icons';
import TimeSlotSelection from './TimeSlotSelection.js';
import Checkbox from '@material-ui/core/Checkbox';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import all from '../hoc/Icons';

// useEffect() after the render method ran

// props history.replace!!!!!

// make store in index.js

function icon(icon_name) {
  switch (icon_name) {
    case ('icon_stressed'): return icon_stressed
    case ('icon_box_tissue'): return icon_box_tissue
    case ('icon_dringende_nachricht'): return icon_dringende_nachricht
    case ('icon_epilepsy_smartwatch'): return icon_epilepsy_smartwatch
    case ('icon_fernglas'): return icon_fernglas
    case ('icon_finanzmedaille'): return icon_finanzmedaille
    case ('icon_fragen'): return icon_fragen
    case ('icon_frau_mit'): return icon_frau_mit
    case ('icon_freunde'): return icon_freunde
    case ('icon_gefährliche_strömung'): return icon_gefährliche_strömung
    case ('icon_hochzeitstag'): return icon_hochzeitstag
    case ('icon_info'): return icon_info
    case ('icon_konferenztelefonat'): return icon_konferenztelefonat
    case ('icon_konflikt'): return icon_konflikt
    case ('icon_kopfschmerzen'): return icon_kopfschmerzen
    case ('icon_meditationsguru'): return icon_meditationsguru
    case ('icon_person_im'): return icon_person_im
    case ('icon_schlaflosigkeit_durchmachen'): return icon_schlaflosigkeit_durchmachen
    case ('icon_smartphone_tablet'): return icon_smartphone_tablet
    case ('icon_unternehmen'): return icon_unternehmen
    case ('icon_vater'): return icon_vater
    case ('icon_yoga'): return icon_yoga
    case ('icon_zum_schweigen'): return icon_zum_schweigen
    default: return icon_fragen
  }
}

const theme = createMuiTheme({
  palette: {
    text: {
      primary: "#555A64"
    }
  },
  typography: {
    fontFamily: [
      "Nunito", "Segoe UI", "Roboto", "Helvetica Neue", "Arial", "Noto Sans", "sans - serif", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji"
    ].join(','),
    htmlFontSize: 12,
    body1: {
      color: "#555A64"
    },
    body2: {
      color: "#555A64"
    }
  },
});

const input = (props) => {
  let inputElement = null;
  // console.log(props);

  let bold = props.elementConfig != null && props.elementConfig.bold != null ? props.elementConfig.bold === true : false

  let label = <label className={bold ? "bold" : ""}><FormattedMessage id={props.label} /></label>
  switch (props.elementType) {
    case ('input'):
      inputElement =
        <div className="Input">
          {label}
          <input onChange={props.changed}
            className="InputElement form-control" {...props.elementConfig} value={props.value} /></div>;
      break;
    case ('textarea'):
      inputElement = <div className="Input">{label}<textarea
        onChange={props.changed}
        className="InputElement form-control" {...props.elementConfig} /></div>;
      break;

    case ('checkbox'):
      inputElement =
        <div className="Input">
          {label}
          <ThemeProvider theme={theme}>
            <FormControlLabel
              control={
                <Checkbox checked={props.value} onChange={props.changed} value="checkedA" color="primary" />
              }
              label={messages[language][props.elementConfig.text]}
            />
          </ThemeProvider>
        </div>
      break;

    case ('radio'):
      inputElement = <div className="Input">
        {label}
        <ThemeProvider theme={theme}>
          <RadioGroup name="position" value={props.name} onChange={props.changed}>
            {props.elementConfig.options.map(element =>
              // <div>
              //   <Radio color="primary" /> <FormattedMessage id={messages[language][element]} />
              // </div>
              <FormControlLabel className="bold float-left"
                key={element}
                value={element}
                control={<Radio color="primary" />}
                label={messages[language][element]}
                labelPlacement="end"
              ></FormControlLabel>
            )}
          </RadioGroup>
        </ThemeProvider>
      </div >
      break;
    case ('slider'):
      inputElement = <div className="Input">{label}
        <Slider className="option"
          defaultValue={0}
          aria-labelledby="discrete-slider-always"
          step={props.elementConfig.options.size}
          marks={props.elementConfig.options}
          valueLabelDisplay="on" />
      </div >
      break;

    case ('cards-container'):
      inputElement = <div className="Input">
        {label}
        <div className="cards-container" key="cards-container">
          {props.elementConfig.options.map(element =>
            <Card classes={props.selected.includes(element.text) ? "selected" : ""} onClick={() => props.changed(element)} key={element}>
              <Icon src={icon(element.icon)} ></Icon>
              <label>
                <FormattedMessage id={element.text} />
              </label>
            </Card>
          )}
          {props.elementConfig.other != null ?
            <Card classes={props.selected.includes(props.elementConfig.other.text) ? "selected-dropdown" : ""} key={props.elementConfig.other.text} onClick={() => props.changed(props.elementConfig.other)} >
              < Icon src={icon_stressed} ></Icon>
              <label>
                <FormattedMessage id={props.elementConfig.other.text} />
              </label>
              {props.selected.includes(props.elementConfig.other.text) ? <input className="InputElement form-control" /> : null
              }

            </Card>
            : null
          }
        </div >
      </div >
      break;

    case ('button-next'):
    case ('button-submit'):
      inputElement =
        // <div className="Input">
        <button className="btn-primary default-left-margin default-top-margin" onClick={props.changed} type="button"><FormattedMessage id={props.label} />
        </button >
      // </div>
      break;

    case ('button-back'):
      inputElement =
        // <div className="Input">
        <button className="btn-primary-outline float-left default-top-margin" onClick={props.changed} type="button"><FormattedMessage id={props.label} />
        </button >
      // </div>
      break;

    case ('time-slots'):
      inputElement = <TimeSlotSelection {...props} />
      break;

    case ('select'):
      inputElement = (
        <div className="Input">
          {label}
          <select
            onChange={props.changed}
            className='InputElement custom-select dropdown form-control'>
            {props.elementConfig.options.map(element => (
              <FormattedMessage id={element} defaultMessage="" tagName="option" value={element} />
            )
            )}
          </select>
        </div >
      )
  }

  return (
    <div className="default-bottom-margin">
      {inputElement}
    </div>
  );
}

export default input;
