import React from 'react';
import { FormattedMessage } from 'react-intl';
import { Card, Col1_2, Col2_2 } from '../hoc/Grid';
import 'moment-timezone';
import moment from 'moment-timezone';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';

function select(date, time) {
    console.log(date)
    console.log(time)
}

const timeSlotSelection = (props) => {

    let days = props.elementConfig.daysInWeek
    let currentIndex = props.elementConfig.firstDayOffset
    let array = Array.from(Array(days).keys()).map(index => {
        let mom = moment().add(currentIndex, "days")
        if (mom.day() === 0) { // Skip sunday
            mom = mom.add(1, "days")
            currentIndex += 1
        }
        if (mom.day() === 6) { // Skip saturday and sunday
            mom = mom.add(2, "days")
            currentIndex += 2
        }
        mom.locale("de")
        currentIndex++
        return mom
    })
    // console.log("date array")
    console.log(array)
    let timeSlots = props.elementConfig.timeSlots

    return (
        <div className="row">
            <div className="row">
                <Col1_2>
                    <ArrowBackIosIcon className="default-height" />
                    <h4><FormattedMessage id="appointment.calender_move_back"></FormattedMessage> </h4>
                </Col1_2>
                <Col2_2>
                    <div className="align-right">
                        <h4><FormattedMessage id="appointment.calender_move_forward"></FormattedMessage></h4>
                        <ArrowForwardIosIcon />
                    </div>

                </Col2_2>
            </div>
            <table className="table">
                <thead>
                    <tr>
                        {array.map(element => (
                            <th key={element.locale("de-de").format("dddd - DD.MM")}> {element.locale("de-de").format("dddd - DD.MM")} </th>
                        ))}
                    </tr>
                </thead>
                <tbody>
                    {timeSlots.map(cell => (
                        <tr key={cell} key={cell}>
                            {array.map(element => (
                                <td onClick={() => props.changed(cell + element.format("dddd DD.MM.YYYY"))} key={cell + element} className={props.selected.includes(cell + element.format("dddd DD.MM.YYYY")) ? "selected " : ""}>
                                    <Card>
                                        <FormattedMessage id={cell} />
                                    </Card >
                                </td>
                            ))}
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
}

export default timeSlotSelection;
